html,
body {
	margin: 0;
	padding: 0;
	background: #0e120f;
	color: white;
	font-family: sans-serif;
}



#myearth {
	width: 100%;
	height: 70vh;
	overflow: hidden;
	position: absolute;
	opacity: 0;
	transition: opacity 1.5s ease-in;
}

#myearth.earth-ready {
	opacity: 1;
}

#myearth::before {
	content: none;
}

@media (max-width: 3500px) {
	#myearth{ 
		top: -8%;
	}
}
@media (max-width: 1801px) {
	#myearth{ 
		top: -5%;
		height: 70vh;
	}
}
@media (max-width: 1401px) {
	#myearth{ 
		top: -7%;
	
	}
}
@media (max-width: 1200px) {
	#myearth{ 
		top: -5%;
	}
}
@media (max-width: 992px) {
	#myearth{ 
		top: 2%;
	}
}
@media (max-width: 768px) {

}
@media (max-width: 576px) {
	#myearth{ 
		top: 4%;
		height: 50vh;
	}
}